import { request } from './request.js'

// 招聘节首页聚合接口
export const getIndexMsgApi = (params) => request({
	url: `/festival/index`,
	method: 'get',
	params
})

// 热门公司分页列表
export const getHotCompanyListApi = (params) => request({
	url: `/festival/hot_list/company`,
	method: 'get',
	params
})

// 一线大厂公司人气点赞接口
export const favorApi = (params) => request({
	url: `/festival/favor`,
	method: 'post',
	params
})

// 一线大厂公司人气点赞接口
export const testFavorApi = (params) => request({
	url: `/test/festival/favor`,
	method: 'get',
	params
})

// 入驻提交接口
export const addApplyApi = (params) => request({
	url: `/festival/form/apply`,
	method: 'post',
	params
})

// 用户点击记录接口
export const touchApi = (params) => request({
	url: `/touch`,
	method: 'post',
	params
})


// 招聘节首页聚合接口v2
export const getIndexMsgV2Api = (params) => request({
	url: `/festival/index/v2`,
	method: 'get',
	params
})

// 招聘节首页聚合接口v2
export const getIndexMsgV2TestApi = (params) => request({
	url: `/test/festival/index/v2`,
	method: 'get',
	params
})



