<script>
import Vue from 'vue'
import Component from 'vue-class-component'
import axios from 'axios'
import wechatMixin from '@/mixins/wechat'
import {getIndexMsgV2Api, getIndexMsgV2TestApi,testFavorApi, favorApi, addApplyApi, touchApi} from '@/store/api/available'
import localstorage from '@/utils/localstorage'


const wx = Vue.wx
@Component({
  components: {
  },
  mixins: [wechatMixin]
})
export default class PageIndex extends Vue {
  // cdn 图片路径
  cdnPath = `${process.env.VUE_APP_CDNPATH}/available/`
  // 分享的数据
  infos = {}
  batCompanyInfo = []   // 一线大厂
  vkeyArr = [] // vkey 按钮
  pop = {
    isShow: false,
    showType: 'one'
  }
  isShowShare = false

  imgload(index) {
    let imgSize = this.$refs['imgSize'][index]

    let height = imgSize.naturalHeight
    let width = imgSize.naturalWidth

    if(height>width){
      this.batCompanyInfo[index].imgtype = 'type2' 

    }
  }

  mounted () {
    wx.miniProgram.getEnv(function (res) {
      wx.miniProgram.postMessage({data: 2222})
    })
    localstorage.set('sessionToken', this.$route.query.sessionToken)
    localstorage.set('token', this.$route.query.token)
    this.appendHead()
    setTimeout(() => {
      this.getList()
      this.firstGetCityCompany()
    }, 500)
    this.wechatShare({
      title: '百亿资本加持下的风口新机遇火热来袭，顶级投资机构专场只等你来',
      desc: '招聘营销变得必不可少，一起开启你的社交招聘',
      link: location.href.split('#')[0],
      imgUrl: `${this.cdnPath}share_img_v2.png`
    })
  }

  // 统计
  touchStatistics(vkey){
    let data = {
      vkey : vkey,
      from : 1,
    }
    touchApi(data)
  }

  setScroll() {
    let hot_city_nav = document.querySelector('.hotCitiesCont').offsetTop
    let hotCont = document.querySelector('.hotCont').offsetTop
    let clientHeight = this.getClientHeight()
  }

  getList() {
    let data = {
      count: 20,
      page: 1
    }
    getIndexMsgV2Api().then(res => {
      const {batCompanyInfo, vkeyArr } = res.data

      batCompanyInfo.map(item => {
        item.imgtype = ''
      })
      this.batCompanyInfo = batCompanyInfo
      this.vkeyArr = vkeyArr
      this.joinNumOp()
      wx.miniProgram.postMessage({data: res.data})
    }).catch(err => {
    })
  }

  toSharePic() {
    let imgUrl = encodeURIComponent('https://attach.lieduoduo.ziwork.com/front-assets/images/hssshare.jpg')

    wx.miniProgram.navigateTo({
      url: `../poster/saveImg/saveImg?url=${imgUrl}`
    })
  }


  // 一线大厂点击
  clickHotCompany(id,index) {
    let item = this.batCompanyInfo[index]
    this.touchStatistics(item.vkey)
    if(!item.isHotFavored){
      favorApi({
        //user_wechat_id: 155,
        company_id: id
      }).then(res => {
        item.isHotFavored = 1
      })
      item.hotFavorCount+=1
    }
    this.toCompanyXcx(id)
  }

  // 去公司职位
  toCompanyXcx(id,vkey) {
    if (vkey) this.touchStatistics(vkey)
    wx.miniProgram.navigateTo({
      url: `../homepage/homepage?companyId=${id}`
    })
  }

  toIndex(style) {
    if (style === 'index')  this.touchStatistics(this.vkeyArr.goBackIndex.vkey)
    else if (style === 'toMorePosition') this.touchStatistics(this.vkeyArr.discoverMorePosition.vkey)

    wx.miniProgram.reLaunch({
      url: `../../../../page/applicant/pages/index/index?sourceType=zpj`
    })
  }

  toAdress (num) {
    window.scrollTo({ 
      top: num || 0, 
      behavior: "smooth" 
    });
  }

  openShare () {
    this.pop = {
      isShow: true,
      showType: 'one'
    }
  }

  closeShare () {
    this.isShowShare = false
  }

  toTable() {
    //this.touchStatistics(this.vkeyArr.applyCompany.vkey)
    this.$router.push({
      name: 'availableTable'
    })
  }

  addApply(){
    addApplyApi({
      company_name: '',
      name: '',
      position: '',
      mobile: ''
    })
  }

  /**
   * @detail   获取当前滚动的高度
   * @return   {[type]}   [description]
   */
  getNowScrollTop() {
    let scrollTop = 0
    if(document.documentElement && document.documentElement.scrollTop) {
      scrollTop = document.documentElement.scrollTop
    } else if(document.body) {
      scrollTop = document.body.scrollTop
    }
    return scrollTop
  }
  /**
   * @detail   获取客户端的视口高度
   * @return   {[type]}   [description]
   */
  getClientHeight() {
    let clientHeight = 0
    if(document.body.clientHeight && document.documentElement.clientHeight) {
      clientHeight = (document.body.clientHeight < document.documentElement.clientHeight) ? document.body.clientHeight : document.documentElement.clientHeight
    } else {
      clientHeight = (document.body.clientHeight>document.documentElement.clientHeight) ? document.body.clientHeight : document.documentElement.clientHeight
    }
    return clientHeight
  }
  /**
   * @detail   添加网站的关键词和描述
   * @return   {[type]}   [description]
   */
  appendHead() {
    var head = document.getElementsByTagName('head')[0]
    // 添加关键词
    var meta_keywords = document.createElement('meta')
    meta_keywords.name = 'keywords'
    meta_keywords.content = 'meta_keywords'
    head.appendChild(meta_keywords)

    var meta_description = document.createElement('meta')
    meta_description.name = '猎多多  社交求职招聘神器'
    meta_description.content = '招聘营销变得必不可少，一起开启你的社交招聘'
    head.appendChild(meta_description)
  }

  //判断是否是微信浏览器的函数
  get isWeiXin() {
    var ua = window.navigator.userAgent.toLowerCase();
    return ua.match(/MicroMessenger/i) == 'micromessenger'
  }
}
</script>
